.btn {
    @include ux-accessible-focus-border();
}

.btn-warning {
    color: white;

    &:focus {
        outline-color: #0058d9;
    }
}

.btn-outline-warning:focus {
    outline-color: #0058d9;
}

.btn-accent, .btn-secondary, .btn-flat {
    &:focus {
        outline-color: #0014c4;
    }
}
